@charset "UTF-8";
@import url(~antd/dist/antd.css);
body, div, ul, li, ol, h1, h2, h3, h4, h5, h6, input, textarea, select, p, dl, dt, dd, a, img, button, form, table, th, tr, td, tbody, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  margin: 0;
  padding: 0; }

body {
  background: #f0f2f5; }

/* 标签页 */
/* 表单 */
.account, .accountsdetails, .reward, .tools {
  margin: 100px 40px 20px 40px;
  min-height: 70vh; }
  .account .creatname, .accountsdetails .creatname, .reward .creatname, .tools .creatname {
    margin-left: 15px; }

/* 工具 */
.tools .toolsHead .acount {
  margin-bottom: 15px; }

.tools .toolsHead .progress {
  display: flex;
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 10px; }
  .tools .toolsHead .progress .ant-progress-line {
    width: 80%; }

.tools .toolsHead .progressNum {
  color: #3080fe;
  font-size: 14px;
  padding-top: 20px; }

.tools .content {
  padding-left: 30px;
  width: 70%; }

.tools .transactionID {
  padding-top: 15px;
  color: #3080fe; }

@media (max-width: 576px) {
  .account .creatname {
    margin-left: 15px; } }
