@charset "UTF-8";
/* 卡片 */
.card {
  margin-top: 60px; }
  .card .cardlist {
    padding: 3em; }
  .card .cardtwo {
    padding: 0 3em; }
  .card span {
    color: rgba(0, 0, 0, 0.45);
    font-size: 14px;
    line-height: 22px; }
  .card h3 {
    font-size: 30px;
    line-height: 38px;
    color: #000000;
    padding: 8px; }
  .card .fibos_eos {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }

@media (max-width: 768px) {
  .card .cardlist {
    padding: 1em; } }
