body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@charset "UTF-8";
body, div, ul, li, ol, h1, h2, h3, h4, h5, h6, input, textarea, select, p, dl, dt, dd, a, img, button, form, table, th, tr, td, tbody, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  margin: 0;
  padding: 0; }

body {
  background: #f0f2f5; }

/* 标签页 */
/* 表单 */
.account, .accountsdetails, .reward, .tools {
  margin: 100px 40px 20px 40px;
  min-height: 70vh; }
  .account .creatname, .accountsdetails .creatname, .reward .creatname, .tools .creatname {
    margin-left: 15px; }

/* 工具 */
.tools .toolsHead .acount {
  margin-bottom: 15px; }

.tools .toolsHead .progress {
  display: -webkit-flex;
  display: flex;
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 10px; }
  .tools .toolsHead .progress .ant-progress-line {
    width: 80%; }

.tools .toolsHead .progressNum {
  color: #3080fe;
  font-size: 14px;
  padding-top: 20px; }

.tools .content {
  padding-left: 30px;
  width: 70%; }

.tools .transactionID {
  padding-top: 15px;
  color: #3080fe; }

@media (max-width: 576px) {
  .account .creatname {
    margin-left: 15px; } }

@charset "UTF-8";
/* 头部 */
.ant-layout-header {
  position: fixed;
  width: 100%;
  z-index: 1;
  background: #ffffff;
  padding: 0; }
  .ant-layout-header .ant-form {
    line-height: 60px; }
  .ant-layout-header .logo {
    float: left;
    padding-left: 100px;
    padding-right: 60px; }
    .ant-layout-header .logo a {
      color: #000000;
      text-decoration: none; }
  .ant-layout-header .ant-menu-horizontal {
    width: 100%;
    margin: auto;
    height: 64px;
    line-height: 64px;
    border-bottom: none; }
  .ant-layout-header .smallmenuImg {
    float: left;
    width: 32px;
    margin-top: 20px;
    margin-right: 20px; }
  .ant-layout-header .LronmanName {
    margin-right: 10px;
    color: #1890ff;
    font-size: 12px; }
  .ant-layout-header .serach {
    width: 300px; }

@media (max-width: 576px) {
  .ant-layout-header .serach {
    margin-left: 40px;
    width: 125px; } }

@media (max-width: 768px) {
  .ant-layout-header .logo {
    padding-left: 20px;
    padding-right: 20px; }
  .ant-layout-header .serach {
    margin-left: 40px;
    width: 150px; }
  .ant-menu-submenu-arrow {
    right: 0 !important; } }

.footer {
  text-align: center;
  margin: 3em 0 1em 0; }
  .footer p {
    text-align: center;
    color: rgba(0, 0, 0, 0.45);
    padding: 5px;
    font-size: 12px; }
    .footer p a {
      color: rgba(0, 0, 0, 0.45);
      margin: 0 20px; }

@charset "UTF-8";
/* 卡片 */
.card {
  margin-top: 60px; }
  .card .cardlist {
    padding: 3em; }
  .card .cardtwo {
    padding: 0 3em; }
  .card span {
    color: rgba(0, 0, 0, 0.45);
    font-size: 14px;
    line-height: 22px; }
  .card h3 {
    font-size: 30px;
    line-height: 38px;
    color: #000000;
    padding: 8px; }
  .card .fibos_eos {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }

@media (max-width: 768px) {
  .card .cardlist {
    padding: 1em; } }

.cardlist {
  padding: 3em; }
  .cardlist .p2p {
    letter-spacing: 0.5px; }
  .cardlist .chainID {
    word-break: break-all; }

@media (max-width: 768px) {
  .ant-card {
    margin-bottom: 20px; }
  .cardlist {
    padding: 1em; } }

.main {
  padding: 3em; }
  .main .tabs {
    padding: 10px 50px;
    background: #ffffff; }

@media (max-width: 768px) {
  .main {
    padding: 1em; }
    .main .tabs {
      padding: 10px; } }

.balance {
  color: #666666;
  line-height: 14px;
  font-size: 14px; }

.num {
  margin-top: 20px;
  font-size: 32px;
  font-weight: 500;
  line-height: 30px;
  height: 30px;
  color: #333333; }

.mortgage {
  font-weight: bold; }

.mortgagenum {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 0; }

.fo {
  font-size: 20px;
  font-weight: bold;
  color: #3080fe; }

.number {
  color: #999999; }

.resources {
  padding-top: 20px;
  color: #999999; }

@charset "UTF-8";
/* 账户详情 */
.accountsdetails {
  padding-top: 10px; }
  .accountsdetails .accounts {
    padding: 0 50px;
    padding-bottom: 20px;
    background: #ffffff; }
    .accountsdetails .accounts .ant-collapse-content > .ant-collapse-content-box {
      padding: 16px 0; }
    .accountsdetails .accounts .details {
      padding: 20px 50px;
      border-bottom: 1px solid #f0f0f0; }
      .accountsdetails .accounts .details .created {
        margin-left: 20px;
        font-size: 14px;
        color: #888888; }
  .accountsdetails .currency, .accountsdetails .permissions, .accountsdetails .actions {
    margin-top: 40px;
    padding: 20px 50px;
    background: #ffffff; }
  .accountsdetails .time {
    width: 200px; }
  .accountsdetails .detail {
    width: 80px; }
  .accountsdetails pre {
    white-space: pre-wrap;
    word-wrap: break-word; }

@media (max-width: 768px) {
  .accountsdetails .accounts, .accountsdetails .currency, .accountsdetails .permissions, .accountsdetails .actions {
    padding: 20px 20px; } }

